// bus
// bus
// var bus = new Vue()
Vue.filter('number',value =>{
  if(value > 10000){
    return (value / 10000).toFixed(2) + ''
  }else{
    return value
  }
})
Vue.filter('message',value => {
  if(value.length > 12){
    return value.substr(0,10) + '...'
  }else{
    return value
  }
})


new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



